.custom-button {
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px 0 35px;
  font-size: 15px;
  background-color: rgba(72, 92, 173, 1);;
  color: white;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed';
  font-weight: bolder;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;

  &:hover {
    background-color: white;
    color: black;
    border: 1px solid rgba(72, 92, 173, 1);;
  }

  &.google-sign-in{
    background-color: #4285f4;
    color: white;

    &:hover{
      background-color: #357ae8;
      border: none;
    }
  }

  &.inverted{
    background-color: white;
    color: black;
    border: 1px solid rgba(72, 92, 173, 1);;

    &:hover{
      background-color: rgba(72, 92, 173, 1);;
      color: white;
      border: none;
    }
  }
}